import React from "react";
import { useNavigate  } from 'react-router-dom'
import {NavigateFunction} from "react-router";
import './entry.css';

interface EntryProps {
    goto: NavigateFunction
}

interface EntryState {
    isHover: boolean;
}

class Entry extends React.Component<EntryProps, EntryState> {
    constructor(props: EntryProps) {
        super(props);
        this.state = {
            isHover: false,
        }
    }

    renderSevenStars() {
        return <div className={"stars"}>
            <div className={"star star-ts"}/>
            <div className={"star star-tx"}/>
            <div className={"star star-tj"}/>
            <div className={"star star-tq"}/>
            <div className={"star star-yh"}/>
            <div className={"star star-ky"}/>
            <div className={"star star-yg"} onClick={() => {
                this.props.goto("/rickroll");
            }}/>
        </div>
    }

    renderEntry(label: string, className: string) {
        const {isHover} = this.state;
        return <button className={"circleButton " + className} style={{animationPlayState: isHover ? "paused" : "running"}}
                    onMouseOver={() => this.setState({isHover: true})}
                    onMouseLeave={() => this.setState({isHover: false})}
                    onClick={() => {this.props.goto('/rickroll')
                    }}>
            <div className={"entryLabel"}>
                {label}
            </div>
        </button >
    }

    renderMenu() {
        const {isHover} = this.state;
        return <div className={'path'} style={{animationPlayState: isHover ? "paused" : "running"}}>
            {this.renderEntry("工具", "tool")}
            {this.renderEntry("笔记", "note")}
            {this.renderEntry("文章", "article")}
        </div>
    }

    render() {
        return <div className={"body"}>
            {this.renderMenu()}
            {this.renderSevenStars()}
        </div>
    }
}

function EntryWithHistory() {
    const navigate = useNavigate();
    return <Entry goto={navigate}/>
}

export default EntryWithHistory;